.projects-section {
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden; /* prevents accidental horizontal scrolling */
  padding: 4rem 2rem;
  text-align: center;
}

.projects-heading {
  font-size: 3.5rem;
  font-weight: bold;
  color: var(--dark-green);
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* ensures 2 columns */
  gap: 0;
}

.project-block {
  background-color: transparent;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  color: #333;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: var(--dark-green);
  margin-bottom: 1rem;
}

.project-description {
  font-size: 1.2rem;
  line-height: 1.6;
}

.project-block:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.section {
  display: flex;
  flex-direction: column; /* Stack blocks vertically */
  align-items: center; /* Center blocks horizontally */
  gap: 20px; /* Space between blocks */
  padding: 20px;
}

.section .block {
  width: 80vw; /* Fixed width: 80% of the viewport */
  max-width: 1200px; /* Optional: Limit the width for very wide screens */
  padding: 20px; /* Add padding inside the blocks */
  background-color: #000; /* Black background */
  color: #00ff00; /* Neon green text */
  text-shadow: 0 0 8px #00ff00; /* Glowing text effect */
  border: 2px solid #00ff00; /* Neon green border */
  box-shadow: 0 4px 10px rgba(0, 255, 0, 0.5); /* Neon green shadow */
  border-radius: 10px; /* Rounded corners */
  font-size: 1.2rem; /* Adjusted font size */
  box-sizing: border-box; /* Ensure padding doesn't affect the size */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth hover effect */
}

.section .block:hover {
  transform: scale(1.02); /* Slightly enlarge on hover */
  box-shadow: 0 8px 20px rgba(0, 255, 0, 0.7); /* Enhance shadow on hover */
}
